.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  width: 100vw;
  text-align: left;
  background-image: url("./images/childer.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 150px;
  padding: 20px;
  color: white;
}

.tagline {
font-size: 24px;
font-weight: 400;
padding: 50px 80px;
}

.logo {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 150px;
}

.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  width: 100%;
}

.button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  outline: none;
  background: rgba(0, 0, 0, 0.1);
  padding: 50px;
  border: none;
  margin: 0 20px;
  font-size: 28px;
  font-weight: 500;
  color: white;
  width: 240px;
  cursor: pointer;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.button:hover {
  color: #1B3F86;
  background-color: rgba(255, 255, 255, 0.6);
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

@-webkit-keyframes logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



